export const cartReducer = (state = {
	items: [],
	itemsAmount: 0,
	cartWorth: 0.00
}, action) => {
	switch (action.type) {
		case 'ADD_TO_CART':
		//payload is an array: [id, price]
			return {
				...state,
				items: {...state.items, 
					[action.payload[0]]: state.items[action.payload[0]]+1 || 1
				},
				itemsAmount: state.itemsAmount + 1,
				cartWorth: state.cartWorth + action.payload[1]
			}
		case 'REMOVE_FROM_CART':
			//payload is an array: [id, price]
			return {
				...state,
				items: {
					...state.items,
					[action.payload[0]]: state.items[action.payload[0]] - 1
				},
				itemsAmount: state.itemsAmount - 1,
				//- below I'm fixing rounding issue; 
				//- when cart worth is very small fraction, below 0.01, make it 0.00
				cartWorth:  state.cartWorth - action.payload[1] < 0.01
					? Number(0).toFixed(2) : state.cartWorth - action.payload[1]
			}
		default:
			return state;
	}
}
